body{
    background-image: url(back.gif);
    color: white;
}
main{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items:center ;
    gap: 10px;
    width: 100%;
}
section{
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
    width: 80%;
}
input{
    height: 40px;
    backdrop-filter: blur(8px);
    border-left: 1px  solid white;
    border-top: 1px  solid white;
    border-radius: 10px;
    color: white;
    background-color: transparent;
    box-shadow: 0px 5px 10px rgb(8, 153, 193);
}
button{
    height: 40px;
    backdrop-filter: blur(8px);
    border-left: 1px  solid white;
    border-top: 1px  solid white;
    border-radius: 10px;
    font-weight: bold;
    color: white;
    background-color: transparent;
    box-shadow: 0px 5px 10px rgb(8, 153, 193);
}